'use strict';

import mongoose from 'mongoose';
import {Schema} from 'mongoose';
import {registerEvents} from './message.events';

var MessageSchema = new mongoose.Schema({
    message: {
		type:String
	},
    //seller or user
	sentBy:{
		type:String
	}, 
	senderRead:{
		type:Boolean
	},
    // //this field is only added to send message by socket
    // //without using conditional statement
    // //else nowhere it would be used.
    // sendTo:{
    // 	type:String
    // },
	receiverRead:{
		type:Boolean
	},
	//user
	sender: {
		type: Schema.ObjectId,
		ref: 'User'
	},
    //seller 
	receiver: {
		type: Schema.ObjectId,
		ref: 'User'
	},
    timestamp:{
    	type:Number,
    },
	createdAt: {
		type: Date,
		default: Date.now
	}
});

registerEvents(MessageSchema);
export default mongoose.model('Message', MessageSchema);
